/* maitree-200 - latin */
@font-face {
     font-family: 'Maitree';
     font-style: normal;
     font-weight: 200;
     src: url('./fonts/maitree-v4-latin-200.eot'); /* IE9 Compat Modes */
     src: local('Maitree ExtraLight'), local('Maitree-ExtraLight'),
          url('./fonts/maitree-v4-latin-200.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
          url('./fonts/maitree-v4-latin-200.woff2') format('woff2'), /* Super Modern Browsers */
          url('./fonts/maitree-v4-latin-200.woff') format('woff'), /* Modern Browsers */
          url('./fonts/maitree-v4-latin-200.ttf') format('truetype'), /* Safari, Android, iOS */
          url('./fonts/maitree-v4-latin-200.svg#Maitree') format('svg'); /* Legacy iOS */
   }
